import * as React from 'react';
import {
  Card,
  Stack,
  CardMedia,
  CardContent,
  Typography,
  CardActions,
  Button,
} from '@mui/material';
import { Data } from './menuData';
import store from '../store/store';

export default function Product() {
  return (
    <Stack
      direction={{ xs: 'column', sm: 'row' }}
      spacing={{ xs: 1, sm: 2, md: 4 }}
    >
      {Data.map((data) => (
        <Card sx={{ maxWidth: 250 }}>
          <CardMedia
            component="img"
            height="240"
            image={data.image}
            alt={data.name}
          />
          <CardContent>
            <Typography
              sx={{ display: 'inline-block' }}
              gutterBottom
              variant="h5"
              component="div"
            >
              {data.name}
            </Typography>
            <Typography
              sx={{ display: 'inline-block', paddingLeft: '1rem' }}
              gutterBottom
              variant="body1"
              component="div"
              color="text.secondary"
            >
              {data.price}$
            </Typography>
            <Typography
              sx={{ display: 'inline-block', paddingLeft: '1rem' }}
              gutterBottom
              variant="body2"
              component="div"
              color="text.secondary"
            >
              stock: {data.stock}
            </Typography>
          </CardContent>
          <CardActions>
            <Button
              sx={{ py: 1, px: 2 }}
              variant="contained"
              color="primary"
              size="small"
              onClick={store.dispatch()}
            >
              + Add
            </Button>
          </CardActions>
        </Card>
      ))}
    </Stack>
  );
}
